.contacts-page{
  .mistake__items{
    justify-content: space-between;
  }
  .map{
    margin-top: 80px;
  }
}


.products-page{
  padding-bottom: 235px;
  &__items{
    margin-top: 100px;
    display: grid;
    grid-template-columns: 100%;
    gap: 180px;
    @media screen and (min-width: 1400px){
      .product-card{
        &:nth-child(even){
          padding-right: 0;
          padding-left: 65px;
          flex-direction: row-reverse;
        }
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .products-page{
    padding-bottom: 60px;
    &__items{
      gap: 50px;
      margin-top: 40px;
    }
  }
}
