@import "../vars.scss";
.breadcrumbs {
    margin: 25px 0 55px;
    &__item {
        font-size: 14px;
        line-height: 171%;
        position: relative;
        font-weight: 400;
        display: inline;
        &:not(:first-child) {
            &:before {
                content: '';
                display: inline-block;
                width: 15px;
                height: 15px;
                margin: 0 7px;
                background-image: url(../img/bread-arrow.svg);
                background-repeat: no-repeat;
                background-position: center;
                transform: translateY(3px);
            }
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        margin: 12px 0 25px;
    }
}
