.rules {
  padding: 0 0 200px 0;
}
.politics {
  padding: 0 0 60px;
}
.text-content {
  &__title {
    margin-bottom: 40px;
  }
  h3 {
    font-weight: 700;
    font-size: 24px;
    line-height: 34px;
    letter-spacing: 0.01em;
    color: $black;
    margin: 60px 0 20px 0;
  }
  p {
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: $black;
  }
  ul {
    margin: 15px 0;
    li {
      font-weight: 500;
      font-size: 16px;
      line-height: 30px;
      color: $black;
      position: relative;
      padding-left: 15px;
      &:after {
        content: '';
        position: absolute;
        top: 14px;
        left: 0;
        width: 3px;
        height: 3px;
        background-color: $black;
        border-radius: 50%;
      }
    }
  }
}
.text-content--two {
  ul {
    li {
      padding-left: 0;
      &:not(:first-child) {
        margin-top: 15px;
      }
      &:after {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .rules {
    padding: 0 0 100px 0;
  }
}

@media screen and (max-width: 576px) {
  .text-content {
    &__title {
      margin-bottom: 20px;
    }
    h3 {
      font-size: 20px;
      line-height: 30px;
      margin: 30px 0 15px;
    }
    p {
      font-size: 14px;
      line-height: 28px;

    }
    ul {
      margin: 10px 0;
      li {
        font-size: 14px;
        line-height: 28px;
        &:after {
          top: 13px;
        }
      }
    }
  }
  .text-content--two {
    ul {
      li {
        &:not(:first-child) {
          margin-top: 10px;
        }
      }
    }
  }
}