.mistake {
  padding: 0 0 120px;
 &__inner {
   display: flex;
   align-items: center;
 }
  &__left {
    width: 715px;
  }
  &__right {
    margin-left: 150px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__title {
    color: $primary;
  }
  &__text {
    font-weight: 500;
    font-size: 18px;
    line-height: 156%;
    color: $black;
    margin-top: 30px;
  }
  &__items {
    display: flex;
    flex-wrap: wrap;
    margin-top: 15px;
  }
  &__item {
    display: flex;
    align-items: center;
    margin: 60px 60px 0 0;
    min-width: 250px;
    &-icon {
      width: 60px;
      height: 60px;
      border: 1px solid $primary;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
    }
    &-desc {
      margin-left: 20px;
    }
    &-subtitle {
      font-weight: 800;
      font-size: 16px;
      line-height: 130%;
      color: $primary;
    }
    &-info {
      font-weight: 500;
      font-size: 16px;
      line-height: 156%;
      margin-top: 5px;
      color: $black;
    }
  }
  &__socials {
    margin-top: 75px;
    display: flex;
    align-items: center;
    &-subtitle {
      font-weight: 800;
      font-size: 16px;
      line-height: 25px;
      color: $primary;
    }
  }
  &__socials-links {
    display: flex;
    align-items: center;
    margin-left: 20px;
  }
  &__social-link {
    width: 35px;
    height: 35px;
    &:not(:last-child) {
      margin-right: 8px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .mistake {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      margin: 50px auto 0;
      max-width: 670px;
    }
  }
}

@media screen and (max-width: 576px) {
  .mistake {
    &__text {
      margin-top: 20px;
    }
    &__items {
      display: block;
      margin-top: 30px;
    }
    &__item {
      margin: 0;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
    &__socials {
      margin-top: 30px;
    }
  }
}
