.footer{
  padding: 109px 0 32px;
  background: #F4F7FF;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__links{
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    max-width: 820px;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 30px;
    }
  }
  &__title{
    font-weight: 800;
    font-size: 16px;
    line-height: 172%;
    margin-bottom: 12px;
    color: $primary;
  }
  &__link{
    display: block;
    font-weight: 500;
    font-size: 17px;
    line-height: 120%;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__contacts{
    width: 260px;
    max-width: 100%;
    &-group{
      &:not(:last-child){
        margin-bottom: 40px;
      }
    }
    &-desc{
      font-weight: 500;
      font-size: 16px;
      line-height: 144%;
      margin-top: 15px;
    }
    &-phone{
      font-size: 22px;
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    .social-link{
      width: 35px;
      height: 35px;
    }
  }
  &__logo2 {
    width: 143px;
    display: block;
    svg {
      width: 100%;
      height: auto;
    }
    &:hover svg>g {
      opacity: .6
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 70px;
    &:after{
      content: '';
      display: block;
      width: 260px;
      max-width: 100%;
    }
  }
  &__c,
  &__conf{
    font-weight: 500;
    font-size: 15px;
    line-height: 253%;
    opacity: .5;
  }
  &__logo,
  &__c{
    margin-right: 20px;
  }
  &__conf{
    max-width: 820px;
    flex-grow: 1;
  }
}


.scroll-up {
  position: fixed;
  width: 114px;
  height: 114px;
  border-radius: 50%;
  bottom: 50px;
  right: 35px;
  z-index: 8;
  opacity: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 27px 0;
  color: #fff;
  font-weight: 500;
  font-size: 20px;
  line-height: 230%;
  &--active {
    opacity: 1;
  }
  &:hover{
    svg{
      transform: unset;
    }
  }
}


@media screen and (max-width: 1400px){
  .footer{
    &__links,
    &__conf{
      max-width: 700px;
    }
    &__col{
      margin-right: 20px;
    }
  }
}


@media screen and (max-width: 1200px){
  .footer{
    &__top{
      flex-wrap: wrap;
    }
    &__logo{
      width: 100%;
      img{
        width: 210px;
      }
    }
    &__links,
    &__contacts{
      margin-top: 30px;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 55px 0 16px;
    &__links{
      display: none;
    }
    &__contacts{
      &-group{
        &:not(:last-child){
          margin-bottom: 25px;
        }
      }
    }
    &__logo2{
      margin-top: 30px;
    }
    &__bottom{
      display: block;
      margin-top: 30px;
    }
  }

  .scroll-up{
    padding: 0;
    justify-content: center;
    width: 50px;
    height: 50px;
    span{
      display: none;
    }
  }
}
