.display1{
  font-weight: 500;
  font-size: 72px;
}

.display2{
  font-weight: 500;
  font-size: 52px;
  line-height: 119%;
}

.body-text{
  font-size: 20px;
  line-height: 120%;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
}

.m-list{
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  li{
    line-height: inherit;
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-bottom: 15px;
    }
    &:before{
      content: '';
      display: block;
      margin-right: 11px;
      width: 14px;
      height: 14px;
      flex-shrink: 0;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='14' height='14' viewBox='0 0 14 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_921_3099)'%3E%3Cpath d='M5.98116 12.6566C5.85341 12.8051 5.63245 12.8164 5.49015 12.6818L0.110918 7.5905C-0.0313805 7.45584 -0.0375674 7.22925 0.0970179 7.08695L1.81087 5.27596C1.94554 5.13366 2.17212 5.12747 2.31442 5.26206L5.25288 8.04303C5.39518 8.1777 5.61614 8.16637 5.74389 8.01788L11.4846 1.34767C11.6124 1.19919 11.8385 1.18223 11.9869 1.31007L13.8765 2.9365C14.025 3.06426 14.042 3.29036 13.9141 3.43885L5.98116 12.6566Z' fill='%232E56BD'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_921_3099'%3E%3Crect width='14' height='14' fill='white'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-position: center;
    }
  }
}

.accent-text{
  color: $primary;
}


@media screen and (max-width: 1400px){
  .display1{
    font-size: 65px;
  }

  .body-text{
    font-size: 18px;
  }
}

@media screen and (max-width: 1200px){
  .display1{
    font-size: 48px;
  }

  .display2{
    font-size: 42px;
  }
}


@media screen and (max-width: 576px){
  .display1{
    font-size: 38px;
  }

  .display2{
    font-size: 35px;
  }
}

