@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
@import 'components/_inputs.scss';
@import 'components/_map.scss';

.swiper-scrollbar{
  width: 100%;
  height: 1px;
  background: #EEEEEE;
  &-drag{
    background-color: $black;
  }
}

.nav-block{
  margin-top: 81px;
}

.more-btn{
  height: 57px;
  width: 100%;
  font-size: 16px;
  svg{
    margin-left: 12px;
    fill: none;
  }
  &:hover{
    path{
      fill: none;
      stroke: #fff;
    }
  }
}

.pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 27px;
  &__item,
  &__next{
    border-radius: 50px;
    border: $border;
    height: 39px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
    min-width: 39px;
    font-weight: 700;
    font-size: 14px;
    flex-shrink: 0;
    margin: 0 8px;
    &:hover,
    &--active{
      background-color: $primary;
      border-color: $primary;
      color: #fff;
      path{
        stroke: #fff;
      }
    }
    &--active{
      pointer-events: none;
    }
    &:first-child{
      margin-left: 0;
    }
  }
  &__next{
    padding: 0 16px;
    margin-right: 0;
    svg{
      margin-left: 10px;
    }
  }
  &__dots{
    font-weight: 700;
    font-size: 14px;
    margin: 0 10px;
  }
}


@media screen and (max-width: 576px){
  .pagination{
    &__next{
      padding: 0;
      width: 39px;
      span{
        display: none;
      }
      svg{
        margin: 0;
      }
    }
  }
}
