.advantage-card{
  max-width: 220px;
  &__icon{
    display: block;
    height: 80px;
    margin-bottom: 40px;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
}

.project-card{
  border-radius: 20px;
  overflow: hidden;
  display: block;
  &__img{
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    padding-bottom: 108.85%;
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
    img{
      transition: all .3s;
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      top: 0;
      left: 0;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(11, 21, 30, 0) 0%, rgba(11, 21, 30, 0.8) 100%);
      z-index: 2;
    }
  }
  &__content{
    pointer-events: none;
    position: absolute;
    z-index: 3;
    padding: 42px;
    bottom: 0;
    left: 0;
  }
  &__images{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    img{
      margin: 10px 15px 0 0;
      flex-shrink: 0;
      height: 40px;
      width: auto;
    }
  }
  &__title{
    display: block;
    font-weight: 500;
    font-size: 22px;
    line-height: 118%;
    margin-top: 25px;
    color: #fff;

  }
  &__link{
    display: flex;
    align-items: center;
    margin-top: 27px;
    font-weight: 700;
    font-size: 15px;
    line-height: 133%;
    color: #fff;
    svg{
      width: 24px;
      height: 24px;
      stroke: #fff;
    }
    &:hover{
      color: rgba(#fff,.5);
      svg{
        stroke: rgba(#fff,.5);
      }
    }
  }
}

.service-card{
  display: block;
  position: relative;
  padding-bottom: 100%;
  border-radius: 50px;
  overflow: hidden;
  transition: all .3s;
  &:hover{
    transform: scale3d(1.15,1.15,1.15);
    z-index: 2;
  }
  &__num{
    position: absolute;
    top: 21px;
    right: 28px;
    font-weight: 300;
    font-size: 30px;
    line-height: 110%;
    color: #fff;
    opacity: 0.3;
  }
  &__round{
    position: absolute;
    border-radius: 50%;
    opacity: 0.3;
    pointer-events: none;
  }
  &__round1{
    width: 309px;
    height: 309px;
    top: -102px;
    right: -88px;
  }
  &__round2{
    width: 455px;
    height: 455px;
    top: -175px;
    right: -161px;
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 50px 46px;
  }
  &__icon{
    display: block;
    height: 112px;
    margin-bottom: 38px;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
  &__title{
    min-height: 66px;
    font-size: 26px;
    line-height: 127%;
    color: #FFFFFF;
    display: flex;
    align-items: flex-end;
    max-width: 325px;
  }
}

.product-card{
  padding: 60px 60px 70px;
  background: #FFFFFF;
  border: 1px solid #F6F8FF;
  box-shadow: 0px 35px 30px 5px rgba(52, 93, 197, 0.05);
  border-radius: 50px;
  display: flex;
  flex-direction: column;
  &__icon{
    height: 85px;
    display: flex;
    align-items: center;
    margin-bottom: 48px;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__title{
    margin-bottom: 35px;
    font-weight: 500;
    font-size: 34px;
    line-height: 124%;
  }
  &__desc{
    font-weight: 500;
    font-size: 16px;
    line-height: 188%;
  }
  &__items{
    margin-top: 40px;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 40px;
  }
  &__item{
    max-width: 565px;
    display: flex;
    margin-top: 30px;
    &:not(:last-child){
      margin-right: 75px;
    }
    &-icon{
      flex-shrink: 0;
      margin-right: 20px;
      width: 37px;
      img{
        width: 100%;
        display: block;
      }
    }
    &-title{
      font-weight: 700;
      font-size: 15px;
      line-height: 107%;
      color: $primary;
    }
    &-desc{
      margin-top: 10px;
      font-weight: 700;
      font-size: 16px;
    }
  }
  &__link{
    margin-top: auto;
    width: 180px;
    height: 60px;
    svg{
      width: 24px;
      height: 24px;
      margin-left: 10px;
    }
  }
  &__img{
    width: 38.1%;
    flex-shrink: 0;
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50px;
    }
  }
  &__content{
    padding: 42px 20px 52px;
    max-width: 910px;
  }
}

.product-card2{
  flex-direction: row;
  justify-content: space-between;
  padding: 0 65px 0 0;
}

.team-card{
  display: flex;
  align-items: center;
  &__img{
    display: block;
    flex-shrink: 0;
    width: 235px;
    margin-right: 36px;
    &-wrap{
      position: relative;
      padding-bottom: 141%;
      width: 100%;
      display: block;
      border-radius: 160px;
      overflow: hidden;
    }
    img{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
    }
  }
  &__content{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title{
    font-weight: 700;
    font-size: 24px;
    line-height: 142%;
    letter-spacing: 0.01em;
    width: 100%;
  }
  &__badge{
    margin: 8px auto 0 0;
    border-radius: 40px;
    padding: 0 10px;
    height: 28px;
    display: flex;
    align-items: center;
    background-color: $primary;
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 0.01em;
    color: #FFFFFF;
  }
  &__desc{
    font-weight: 500;
    font-size: 16px;
    line-height: 162%;
    letter-spacing: 0.01em;
    margin: 15px auto 0 0;
    max-width: 425px;
  }
}

.blog-card{
  &__title{
    font-weight: 700;
    font-size: 24px;
    line-height: 142%;
    letter-spacing: 0.01em;
    text-decoration-line: underline;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__img{
    margin-top: 25px;
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    padding-bottom: 68%;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(11, 21, 30, 0) 0%, rgba(11, 21, 30, 0.7) 100%);
      z-index: 2;
      pointer-events: none;
    }
    &-wrap{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      &:hover{
        img{
          transform: scale(1.1);
        }
      }
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all .3s;
      }
    }
  }
  &__info{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 18px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 3;
  }
  &__author{
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 175%;
    color: #fff;
  }
  &__avatar{
    display: block;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;
    margin-right: 15px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__tags{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -8px;
    .project__tag{
      margin: 8px 8px 0 0;
      height: 34px;
      font-size: 14px;
      padding: 0 8px;
      img{
        height: 20px;
        margin-right: 7px;
      }
    }
  }
  &__tag{
    margin-top: 8px;
    margin-left: 8px;
  }
  &__date{
    font-weight: 500;
    font-size: 14px;
    line-height: 200%;
    color: #B4B9CF;
    margin-top: 25px;
    display: block;
  }
  &__desc{
    margin-top: 12px;
    font-weight: 500;
    font-size: 16px;
    line-height: 175%;
  }
  &__link{
    display: flex;
    align-items: center;
    color: $primary;
    font-weight: 700;
    font-size: 15px;
    line-height: 134%;
    margin-top: 14px;
    svg{
      stroke: $primary;
      width: 24px;
      height: 24px;
      margin-left: 1px;
    }
  }
}


@media screen and (max-width: 1400px){
  .product-card{
    &__img{
      width: 100%;
      height: unset;
      max-width: 400px;
    }
    &__content{
      padding: 0;
      margin-top: 25px;
      max-width: unset;
    }
  }

  .product-card2{
    padding: 30px 30px 35px;
    display: block;
  }
}


@media screen and (max-width: 992px){
  .service-card{
    border-radius: 25px;
    &__icon{
      height: 70px;
      margin-bottom: 10px;
    }
    &__content{
      padding: 20px 15px;
    }
    &__title{
      font-size: 20px;
      min-height: 51px;
    }
  }

  .product-card{
    padding: 20px 15px 30px;
    border-radius: 25px;
    &__icon{
      margin-bottom: 24px;
    }
    &__items{
      margin-top: 15px;
    }
    &__img{
      img{
        border-radius: 20px;
      }
    }
    &__title{
      margin-bottom: 20px;
      font-size: 24px;
    }
  }

  .product-card2{
    padding: 20px 15px 30px;
  }

  .team-card{
    flex-direction: column;
    align-items: unset;
    &__img{
      &-wrap{
        width: 130px;
        height: 130px;
        padding: 0;
        border-radius: 50%;
      }
    }
    &__content{
      width: 100%;
    }
  }


}


@media screen and (max-width: 576px){
  .advantage-card{
    &__icon{
      height: 60px;
      margin-bottom: 20px;
    }
  }

  .project-card{
    &__content{
      padding: 15px;
    }
    &__title{
      font-size: 18px;
      margin-top: 15px;
    }
    &__link{
      margin-top: 15px;
    }
  }

  .service-card{
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 15px 20px;
    min-height: 80px;
    &__content{
      position: static;
      display: flex;
      align-items: center;
      padding: 0;
    }
    &__num{
      position: static;
      margin-right: 5px;
    }
    &__icon{
      margin: 0 10px 0 0;
      width: 50px;
      height: unset;
      img{
        width: 100%;
        height: auto;
      }
    }
    &__title{
      font-size: 14px;
      min-height: unset;
      align-items: center;
    }
  }

  .product-card{
    border-radius: 20px;
    &__icon{
      height: 55px;
      margin-bottom: 15px;
    }
    &__desc{
      font-size: 14px;
      line-height: 130%;
    }
    &__items{
      margin: 0;
    }
    &__item{
      margin-right: 0 !important;
      &-icon{
        width: 30px;
        margin-right: 10px;
      }
      &-desc{
        font-size: 13px;
      }
    }
    &__link{
      width: 100%;
    }
  }

  .team-card{
    &__desc{
      font-size: 14px;
    }
  }

  .blog-card{
    &__title{
      font-size: 18px;
    }
    &__img{
      margin-top: 15px;
      border-radius: 10px;
    }
    &__info{
      padding: 10px 5px;
    }
    &__author{
      font-size: 14px;
    }
    &__avatar{
      margin-right: 10px;
      width: 40px;
      height: 40px;
    }
    &__date{
      margin-top: 15px;
    }
    &__desc{
      margin-top: 10px;
      font-size: 14px;
    }
    &__tags{
      .project__tag{
        margin-right: 3px;
        height: 25px;
        font-size: 9px;
        padding-left: 6px;
        padding-right: 6px;
        img{
          height: 17px;
          margin-right: 3px;
        }
      }
    }
  }
}
