.projects-page{
  padding-bottom: 90px;
  &__title{
    margin-bottom: 39px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 30px;
    margin-top: 59px;
  }
}

.project{
  &__title{
    max-width: 1175px;
  }
  &__img{
    width: 100%;
    margin: 55px auto 0;
    position: relative;
    &-wrap{
      &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: linear-gradient(180deg, rgba(17, 23, 50, 0) 0%, rgba(29, 32, 49, 0) 0.01%, rgba(29, 32, 49, 0.6) 100%);
      }
      &>img{
        width: 100%;
        display: block;
      }
    }

  }
  &__items{
    position: absolute;
    bottom: 52px;
    left: 0;
    width: 100%;
    z-index: 2;
    .container{
      display: flex;
      align-items: center;
      flex-wrap: wrap;
    }

  }
  &__tag{
    margin: 12px 12px 0 0;
    display: flex;
    align-items: center;
    height: 52px;
    &-img{
      img{
        height: 100%;
      }
    }
    &-text{
      padding: 0 18px 0 15px;
      border-radius: 50px;
      letter-spacing: 0.01em;
      font-weight: 500;
      font-size: 16px;
      color: #FFFFFF;
      img{
        margin-right: 9px;
        flex-shrink: 0;
      }
    }
    &:hover{
      color: #fff;
      opacity: .8;
    }
  }
}

.project-steps{
  padding: 50px 0;
  margin-top: 35px;
  .container{
    max-width: 1840px;
  }
}

.project-step{
  overflow: hidden;
  padding: 60px;
  display: grid;
  grid-template-columns: 590px auto;
  background: rgba(#E9E6FF,.9);
  border: 1px solid #E0DCF7;
  border-radius: 40px;
  position: relative;
  min-height: 350px;
  &:nth-child(2){
    background: rgba(#B0EFD1,.7);
    border: 1px solid #A6E1C5;
    .project-step__title{
      color: #199984;
    }
    .project-step__icon{
      background: #13AD95;
    }
  }
  &:nth-child(3){
    background: rgba(#D6E1FF,1);
    border: 1px solid #CEDAF8;
    .project-step__title{
      color: #3F73F6;
    }
    .project-step__icon{
      background: #3F73F6;
    }
  }
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 100px;
    width: 1px;
    height: 100%;
    background: rgba(99, 78, 228, 0.5);
    opacity: .3;
  }
  &:not(:last-child){
    margin-bottom: 50px;
  }
  &__img{
    position: absolute;
    left: 220px;
    top: 50%;
    transform: translateY(-50%);
    max-height: calc(100% - 70px);
  }
  &__title{
    position: relative;
    z-index: 2;
    display: flex;
    color: #634EE4;
    font-weight: 500;
    font-size: 34px;
    line-height: 124%;
    &-text{
      min-height: 80px;
      display: flex;
      align-items: center;
    }
  }
  &__icon{
    background: #634EE4;
    flex-shrink: 0;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin-right: 29px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__text{
    position: relative;
    z-index: 2;
    font-weight: 500;
    font-size: 18px;
    line-height: 178%;
    p{
      line-height: inherit;
    }
  }
}

.article{
  &__info{
    position: absolute;
    bottom: 52px;
    left: 0;
    width: 100%;
    z-index: 2;
    .container{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__tags{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: -8px;
    //img{
    //  height: 52px;
    //}
  }
  &__tag{
    margin-top: 8px;
    margin-left: 8px;
  }
  &__author{
    display: flex;
    align-items: center;
    font-weight: 700;
    color: #fff;
    &-info{
      display: flex;
      align-items: center;
      flex-direction: column;
    }
  }
  &__avatar{
    display: block;
    width: 90px;
    height: 90px;
    border-radius: 50%;
    overflow: hidden;
    flex-shrink: 0;
    margin-right: 15px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__name{
    font-size: 24px;
    line-height: 142%;
    letter-spacing: 0.01em;
    width: 100%;
  }
  &__prof{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 0.01em;
    display: flex;
    align-items: center;
    padding: 0 10px;
    height: 28px;
    border-radius: 40px;
    background-color: $primary;
    margin: 8px auto 0 0;
  }
  &__date{
    margin-top: 35px;
    display: block;
    font-weight: 500;
    font-size: 16px;
    line-height: 175%;
    color: #B4B9CF;
  }
  &__text{
    margin-top: 40px;
    font-weight: 500;
    font-size: 16px;
    line-height: 200%;
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 30px;
      }
    }
    img{
      display: block;
      margin: 80px 0;
      width: 100%;
      border-radius: 20px;
    }
  }
}

.share{
  margin-top: 95px;
  display: flex;
  align-items: center;
  &__title{
    font-weight: 700;
    font-size: 16px;
    line-height: 188%;
    letter-spacing: 0.02em;
    margin-right: 22px;
  }
  &__links{
    display: flex;
    align-items: center;
  }
  &__link{
    margin-right: 15px;
    svg{
      fill: #BAC4DE;
    }
    &--ok{
      &:hover{
        svg{
          fill: #F79A38;
          stroke: #F79A38;
        }
      }
    }
    &--vk{
      &:hover{
        svg{
          fill: #7294C7;
          stroke: #7294C7;
        }
      }
    }
    &--fb{
      &:hover{
        svg{
          fill: #4E6297;
          stroke: #4E6297;
        }
      }
    }
    &--tw{
      &:hover{
        svg{
          fill: #1D9BF0;
          stroke: #1D9BF0;
        }
      }
    }
  }
}


@media screen and (max-width: 1400px){
  .projects-page{
    &__grid{
      grid-template-columns: 1fr 1fr;
    }
  }

  .project-step{
    grid-template-columns: 100%;
    gap: 30px;
    &__text{
      padding-left: 109px;
    }
  }
}


@media screen and (max-width: 992px){
  .projects-page{
    &__title{
      margin-bottom: 20px;
    }
    &__grid{
      gap: 20px;
      margin-top: 30px;
    }
  }

  .project-steps{
    margin-top: 17px;
    padding: 25px 0;
  }

  .project-step{
    padding: 20px;
    border-radius: 15px;
    &:before{
      left: 59.5px;
    }
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }

  .project{
    &__items{
      bottom: 15px;
    }
    &__img{
      &-wrap{
        position: relative;
      }
    }
    &__tag{
      margin: 5px 5px 0 0;
      height: 28px;
      font-size: 12px;
      &-text{
        padding-right: 10px;
        padding-left: 8px;
        img{
          height: 19px;
          width: auto;
          margin-right: 5px;
        }
      }
    }
  }

  .article{
    &__info{
      position: static;
      margin-top: 20px;
      .container{
        display: block;
      }
    }
    &__avatar{
      width: 65px;
      height: 65px;
    }
    &__name{
      font-size: 16px;
      color: $black;
    }
    &__prof{
      font-size: 13px;
      height: 20px;
      padding: 0 5px;
    }
    &__tags{
      margin-top: 15px;
    }
    &__tag{
      margin: 5px 5px 0 0;
      img{
        height: 28px;
      }
    }
    &__date{
      margin-top: 25px;
      font-size: 14px;
    }
    &__text{
      margin-top: 20px;
      font-size: 14px;
      line-height: 150%;
      p{
        &:not(:last-child){
          margin-bottom: 15px;
        }
      }
      img{
        margin: 30px 0;
        border-radius: 10px;
      }
    }
  }

  .share{
    margin-top: 40px;
  }
}


@media screen and (max-width: 830px){
  .projects-page{
    &__grid{
      grid-template-columns: 100%;
      gap: 40px;
    }
  }
}


@media screen and (max-width: 768px){
  .project-step{
    gap: 15px;
    &:before{
      display: none;
    }
    &__title{
      font-size: 24px;
      &-text{
        min-height: 65px;
      }
    }
    &__icon{
      margin-right: 15px;
      width: 65px;
      height: 65px;
      img{
        width: 32px;
      }
    }
    &__text{
      padding: 0;
      font-size: 14px;
    }
  }

  .project{
    &__title{
      font-size: 28px;
    }
  }
}
