.main{
  background-image: url(../img/main-bg.svg);
  background-size: cover;
  background-position: center;
  margin-top: -104px;
  padding-top: 100px;
  color: #FFFFFF;
  &__inner{
    padding: 10.91% 0 5%;
    display: grid;
    grid-template-columns: calc(100% - 440px) 420px;
    gap: 20px;
  }
  &__title{
    max-width: 1140px;
    span{
      font-size: 87.5%;
      font-weight: 400;
    }
  }
  &__desc{
    margin-top: 51px;
  }
  &__hint{
    font-size: 14px;
    line-height: 171%;
    opacity: 0.4;
    margin-top: 10.66%;
  }
}

.main-form{
  background: rgba(52, 93, 197, 0.6);
  box-shadow: 0px 10px 20px rgba(28, 38, 83, 0.05), inset 0px 4px 24px rgba(184, 204, 255, 0.06);
  backdrop-filter: blur(15px);
  border-radius: 30px;
  padding: 41px 30px 62px;
  &__title{
    font-weight: 800;
    font-size: 22px;
    line-height: 109%;
    text-align: center;
  }
  &__desc{
    font-size: 20px;
    line-height: 120%;
    text-align: center;
  }
  .fg{
    margin-top: 40px;
  }
  &__check{
    margin-top: 54px;
  }
  &__btn{
    margin-top: 54px;
    width: 100%;
    svg{
      width: 24px;
      height: 24px;
      margin-left: 10px;
    }
  }
}

.advantages{
  margin-top: 14%;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  gap: 80px;
}

.advantages2{
  grid-template-columns: repeat(5,1fr);
  gap: 45px;
}

.slider-block{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: unset;
      max-width: calc(100% - 30px);
    }
  }
}

.trust{
  padding: 65px 0;
  margin-top: 40px;
  overflow: hidden;
  .swiper{
    &-slide{
      width: 240px;
      height: 156px;
      display: flex;
      align-items: center;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-scrollbar{
      margin-top: 79px;
    }
  }
  .slider{
    margin-top: 65px;
  }
}

.geography{
  padding: 65px 0;
  &__map{
    height: 880px;
    margin-top: 56px;
  }
}

.projects{
  padding: 65px 0;
  overflow: hidden;
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title{
    flex-shrink: 0;
  }
  &__links{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &__link{
    margin: 10px 18px 0 0;
    border-radius: 90px;
    border: $border;
    padding: 0 25px;
    height: 56px;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    img{
      flex-shrink: 0;
      margin-right: 15px;
    }
    &:hover{
      border-color: $black;
      color: $black;
    }
  }
  .slider{
    margin-top: 80px;
  }
  .swiper{
    &-slide{
      width: 533px;
    }
    &-scrollbar{
      margin-top: 99px;
    }
  }
}

.consult{
  background-image: url(../img/main-bg.svg);
  background-size: cover;
  background-position: center;
  color: #fff;
  margin-top: 35px;
  padding: 114px 0 165px;
  .container{
    max-width: 1440px;
  }
  &__title{
    text-align: center;
    font-weight: 400;
  }
  &__desc{
    margin: 40px auto 0;
    width: 1213px;
    max-width: 100%;
    font-size: 18px;
    line-height: 156%;
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px 30px;
    margin-top: 85px;
  }
  .fg{
    input{
      height: 64px;
    }
  }
  &__full{
    grid-column: 1/-1;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 90px;
    max-width: 886px;
  }
  &__check{
    margin: 28px 10px 0 0;
  }
  &__btn{
    margin-top: 28px;
    font-size: 18px;
    color: $black;
    width: 392px;
    svg{
      width: 24px;
      height: 24px;
      margin-left: 10px;
      stroke: $black;
    }
    &:hover{
      svg{
        stroke: $primary;
      }
    }
  }
}

.service{
  padding: 65px 0;
  margin-top: 25px;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 30px;
    margin-top: 65px;
  }
  @media screen and (min-width: 768px){
    .service-card{
      &:nth-child(5){
        padding-bottom: unset;
        height: 100%;
        grid-column: span 2;
      }
    }
  }
}

.products{
  overflow: hidden;
  padding: 80px 0 130px;
  margin-top: 55px;
  background-image: url(../img/broduct-bg.webp);
  background-size: cover;
  background-position: center;
  .slider{
    margin-top: 75px;
  }
  .swiper{
    &-slide{
      width: 957px;
    }
    &-scrollbar{
      margin-top: 127px;
    }
  }
}

.team{
  overflow: hidden;
  padding: 65px 0;
  margin-top: 15px;
  &--blue{
    background: #F4F7FF;
  }
  .slider{
    margin-top: 94px;
  }
  .swiper{
    &-slide{
      width: 800px;
    }
    &-scrollbar{
      margin-top: 139px;
    }
  }
}

.contacts{
  padding: 60px 0 0;
  &__content{
    margin-top: 75px;
    position: relative;
  }
}

.map{
  height: 860px;
  position: relative;
}

.contacts-card{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: calc((100% - 1660px) / 2);
  padding: 65px 45px 50px;
  background: #FFFFFF;
  box-shadow: 0px 45px 35px rgba(207, 214, 232, 0.15);
  border-radius: 20px;
  z-index: 2;
  width: 533px;
  &__group{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 25px;
    padding-bottom: 40px;
    margin-bottom: 40px;
    border-bottom: $border;
  }
  &__item{
    display: flex;
  }
  &__full{
    grid-column: 1/-1;
  }
  &__icon{
    width: 22px;
    flex-shrink: 0;
    margin-right: 15px;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-weight: 800;
    font-size: 15px;
    line-height: 167%;
  }
  &__desc{
    font-weight: 500;
    font-size: 18px;
    line-height: 139%;
    margin-top: 5px;
    max-width: 340px;
  }
  &__subtitle{
    font-weight: 700;
    font-size: 20px;
    line-height: 125%;
    margin-bottom: 30px;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 207px;
    flex-shrink: 0;
    svg{
      width: 15px;
      height: 15px;
      margin-right: 12px;
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    .social-link{
      width: 35px;
      height: 35px;
    }
  }
}


@media screen and (max-width: 1720px){
  .contacts-card{
    left: 30px;
  }
}


@media screen and (max-width: 1600px){
  .main{
    margin-top: -93px;
  }

  .advantages{
    gap: 30px;
  }

  .advantages2{
    grid-template-columns: repeat(4,1fr);
  }

  .projects{
    &__top{
      display: block;
    }
    &__links{
      margin-top: 20px;
    }
  }
}


@media screen and (max-width: 1400px){
  .main{
    margin-top: -83px;
    &__inner{
      padding: 7% 0 9%;
    }
  }

  .advantages{
    margin-top: 7%;
    grid-template-columns: repeat(3,1fr);
  }

  .service{
    &__items{
      grid-template-columns: repeat(3,1fr);
    }
  }

  .contacts-card{
    left: 20px;
  }
}


@media screen and (max-width: 1200px){
  .main{
    margin-top: -79px;
    padding-top: 80px;
    &__inner{
      padding: 3% 0 5%;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    &__inner{
      grid-template-columns: 100%;
      padding: 0 0 40px;
    }
    &__desc{
      margin-top: 25px;
    }
    &__hint{
      margin-top: 50px;
    }
  }

  .trust{
    padding: 32px 0;
    margin-top: 20px;
  }

  .geography{
    padding: 32px 0;
    &__map{
      height: 500px;
      margin-top: 23px;
    }
  }

  .projects{
    padding: 32px 0;
    .slider{
      margin-top: 40px;
    }
    &__links{
      width: calc(100% + 40px);
      margin-left: -20px;
      overflow-x: auto;
      flex-wrap: unset;
      padding: 0 20px;
      &::-webkit-scrollbar {
        height: 0;
        width: 0;
      }
    }
    &__link{
      margin-right: 10px;
      flex-shrink: 0;
    }
    .swiper-scrollbar{
      margin-top: 50px;
    }
  }

  .consult{
    padding: 40px 0 50px;
    margin-top: 18px;
    &__desc{
      margin-top: 20px;
    }
    &__grid{
      margin-top: 40px;
      gap: 30px 20px;
    }
    &__bottom{
      margin-top: 20px;
    }
  }

  .service{
    margin-top: 12px;
    padding: 32px 0;
    &__items{
      margin-top: 32px;
      gap: 20px;
    }
  }

  .slider-block{
    .swiper{
      &-slide{
        max-width: calc(100% - 15px);
      }
    }
  }

  .products{
    padding: 40px 0 65px;
    margin-top: 27px;
    .swiper{
      &-slide{
        width: 700px;
      }
      &-scrollbar{
        margin-top: 50px;
      }
    }
    .slider{
      margin-top: 37px;
    }
  }

  .team{
    padding: 32px 0;
    margin-top: 7px;
    .slider{
      margin-top: 47px;
    }
    .swiper{
      &-slide{
        width: 500px;
      }
      &-scrollbar{
        margin-top: 50px;
      }
    }
  }

  .contacts{
    padding-top: 30px;
    &__content{
      margin-top: 37px;
    }
  }

  .map{
    height: 600px;
  }

  .contacts-card{
    position: static;
    width: 100%;
    transform: unset;
    box-shadow: none;
    background-color: transparent;
    padding: 0 20px 20px;
  }
}


@media screen and (max-width: 768px){
  .advantages{
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }

  .trust{
    .slider{
      margin-top: 32px;
    }
    .swiper{
      &-slide{
        height: 130px;
      }
      &-scrollbar{
        margin-top: 40px;
      }
    }
  }

  .consult{
    &__grid{
      grid-template-columns: 100%;
    }
    &__btn{
      width: 100%;
    }
  }

  .service{
    &__items{
      grid-template-columns: 1fr 1fr;
    }
  }
}


@media screen and (max-width: 576px){
  .main-form{
    width: calc(100% + 30px);
    margin: 20px 0 0 -15px;
    padding: 25px 15px 30px;
    .fg{
      margin-top: 20px;
    }
    &__check,
    &__btn{
      margin-top: 34px;
    }
  }

  .projects{
    &__links {
      width: calc(100% + 30px);
      margin-left: -15px;
      padding: 0 15px;
    }
  }

  .service{
    &__items{
      grid-template-columns: 100%;
      gap: 12px;
    }
  }

  .contacts-card{
    &__group{
      grid-template-columns: 100%;
      margin-bottom: 20px;
      padding-bottom: 20px;
    }
  }

  .map{
    height: 350px;
  }
}
