.about{
  padding: 100px 0 160px;
  overflow: hidden;
  &--page{
    padding-top: 0;
  }
  &__inner{
    display: grid;
    grid-template-columns: 1.08fr 1fr;
    gap: 80px;
    align-items: center;
  }
  &__img{
    img{
      width: 118%;
      border-radius: 448px 0 0 448px;
    }
  }
  &__desc{
    margin-top: 30px;
    font-weight: 500;
    font-size: 16px;
    line-height: 200%;
    max-width: 815px;
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 18px;
      }
    }
  }
  &__subtitle{
    margin-top: 43px;
    font-weight: 500;
    font-size: 28px;
    line-height: 150%;
  }
  &__list{
    margin-top: 19px;
  }
  &__steps{
    margin-top: 50px;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      height: calc(100% - 40px);
      width: 1px;
      left: 22.5px;
      top: 20px;
      background-color: $primary;
    }
    li{
      line-height: inherit;
      display: flex;
      align-items: center;
      position: relative;
      z-index: 2;
      &:before{
        content: '';
        width: 45px;
        height: 45px;
        flex-shrink: 0;
        margin-right: 20px;
        border-radius: 50%;
        background-image: url(../img/check2.svg);
        background-position: center;
        background-repeat: no-repeat;
        background-color: #F4F7FF;
        border: 1px solid $primary;
      }
    }
  }
  &__btn{
    width: 362px;
    height: 70px;
    margin-top: 85px;
    max-width: 100%;
    box-shadow: 0px 14px 24px rgba(46, 105, 255, 0.18);
    svg{
      width: 24px;
      height: 24px;
      margin-left: 10px;
    }
  }
  &--import{
    background: #F4F7FF;
  }
}

.product{
  overflow: hidden;
  background-size: cover;
  background-position: center;
  padding: 118px 0 165px;
  color: #FFFFFF;
  &__inner{
    display: grid;
    grid-template-columns: 48.1% calc(51.9% - 88px);
    gap: 88px;
    align-items: flex-end;
  }
  &__logo{
    img{
      max-width: 100%;
      display: block;
    }
  }
  &__desc{
    font-weight: 500;
    font-size: 17px;
    line-height: 188%;
    margin-top: 40px;
  }
  &__list{
    margin-top: 40px;
    font-weight: 800;
    font-size: 17px;
    &:before{
      margin-right: 15px;
    }
  }
  &__btn{
    width: 362px;
    height: 70px;
    max-width: 100%;
    margin-top: 50px;
    svg{
      width: 24px;
      height: 24px;
      margin-left: 10px;
    }
  }
}

