    }
  }
}

.gallery{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      img{
        object-fit: cover;
        height: 100%;
        width: 100%;
        box-shadow: 0px 34px 101px 30px rgba(33, 78, 196, 0.22);
        border-radius: 10px;
      }
    }
    &-button{
      &-prev{
        left: 0;
      }
      &-next{
        right: 0;
      }
    }
  }
  &__small{
    margin-top: 28px;
    padding: 0 40px;
    position: relative;
    .swiper{
      max-width: 570px;
      margin: 0 auto;
      overflow: unset;
      &-slide{
        opacity: 0;
        &-active,
        &-next,
        &-next+.swiper-slide{
          opacity: 1;
        }
      }
    }
  }
}

.service--page{
  padding-top: 0px;
}


@media screen and (max-width: 1400px){
  .product{
    &__inner{
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
}


@media screen and (max-width: 1200px){
  .about{
    &__inner{
      grid-template-columns: 100%;
      gap: 30px;
    }
  }
}


@media screen and (max-width: 992px){
  .about{
    padding: 50px 0;
    &--page{
      padding-top: 0;
    }
    &__desc{
      line-height: 150%;
      p{
        &:not(:last-child){
          margin-bottom: 9px;
        }
      }
    }
    &__subtitle{
      font-size: 24px;
    }
    &__steps{
      gap: 20px;
      margin-top: 30px;
      li{
        font-size: 16px;
        &:before{
          margin-right: 10px;
        }
      }
    }
    &__btn{
      margin-top: 42px;
    }
  }

  .product{
    padding: 60px 0 80px;
  }
}


@media screen and (max-width: 576px){
  .product{
    &__list{
      font-size: 15px;
    }
  }

  .gallery{
    &__small{
      .swiper{
        &-slide-next+.swiper-slide{
          opacity: 0;
        }
      }
    }
  }
}
