@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .425s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.social-link{
    width: 27px;
    height: 27px;
    &:hover{
        svg{
            transform: translateY(-10px);
        }
    }
    svg{
        width: 100%;
        height: 100%;
        transition: all .3s;
    }
    &:not(:last-child){
        margin-right: 13px;
    }
}

.mbtn-white{
    background: #FFFFFF;
    border-radius: 60px;
    height: 60px;
    font-weight: 700;
    font-size: 16px;
    color: $primary;
    svg{
        stroke: $primary
    }
    &:hover{
        color: $primary;
        background-color: rgba(#fff,.5);
    }
}

.mbtn-black-outline{
    height: 60px;
    border-radius: 60px;
    border: 1px solid $black;
    font-size: 16px;
    font-weight: 700;
    color: $black;
    svg{
        stroke: $black
    }
    &:hover{
        color: #fff;
        background-color: $black;
        svg{
            stroke: #fff
        }
    }
}

.mbtn-blue-outline{
    border: 1px solid $primary;
    border-radius: 60px;
    height: 60px;
    font-weight: 700;
    font-size: 16px;
    color: $primary;
    svg{
        fill: $primary;
    }
    &:hover{
        background-color: $primary;
        color: #fff;
        svg{
            fill: #fff;
        }
    }
}


.mbtn-blue{
    background-color: $primary;
    color: #fff;
    border-radius: 60px;
    font-weight: 700;
    font-size: 16px;
    svg{
        stroke: #fff;
    }
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
        svg{
            transform: translateX(3px);
        }
    }
}
