.modal-backdrop {
    &.show {
        opacity: 0.7;
    }
}

.modal{
    &-dialog{
        pointer-events: unset;
        padding: 62px 63px 78px;
        background: #FFFFFF;
        border-radius: 50px;
        width: 695px;
        max-width: calc(100% - 20px);
    }
    &__close{
        position: absolute;
        top: 25px;
        right: 25px;
        opacity: .2;
        &:hover{
            opacity: 1;
        }
    }
    &__title,
    &__desc{
        text-align: center;
    }
    &__desc{
        font-size: 20px;
        line-height: 140%;
        letter-spacing: 0.01em;
        margin-top: 17px;
    }
    &__form{
        display: flex;
        flex-direction: column;
        align-items: center;
        .fg{
            width: 100%;
            margin: 35px 0 0;
            input{
                height: 64px;
            }
        }
    }
    &__check{
        margin-top: 55px;
    }
    &__nav{
        margin-top: 55px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__btn{
        padding: 0 27px;
        height: 60px;
        font-size: 16px;
        svg{
            width: 24px;
            height: 24px;
            margin-left: 10px;
        }
    }
    &__icon{
        margin: 0 auto 78px;
        width: 212px;
        height: 212px;
        border-radius: 50%;
        background: linear-gradient(0deg, #20439C -31.43%, #345DC5 100%);
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        &:before,
        &:after{
            content: '';
            position: absolute;
            border-radius: 50%;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            pointer-events: none;
        }
        &:before{
            background: linear-gradient(0deg, #20439C -31.43%, #345DC5 100%);
            opacity: 0.03;
            width: 125%;
            height: 125%;
        }
        &:after{
            background: linear-gradient(0deg, #20439C -31.43%, #345DC5 100%);
            opacity: 0.02;
            width: 145%;
            height: 145%;
        }
    }
}


.thank-modal{
    .modal-dialog{
        padding-top: 107px;
    }
}


@media screen and (max-width: 992px){
    .modal{

    }
}


@media screen and (max-width: 768px){
    .modal{
        &-dialog{
            padding: 30px 20px 30px;
            border-radius: 20px;
        }
        &__check,
        &__nav{
            margin-top: 30px;
        }
        &__btn{
            width: 100%;
        }
        &__icon{
            width: 150px;
            height: 150px;
            margin-bottom: 40px;
            img{
                width: 60px;
            }
        }
    }

    .thank-modal{
        .modal-dialog{
            padding-top: 50px;
        }
    }
}
