.site-map{
  padding-bottom: 160px;
  &__grid{
    margin-top: 64px;
    display: grid;
    grid-template-columns: 100%;
    gap: 50px;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: 100%;
      width: 1px;
      left: 57px;
      top: 0;
      border-right: 1px dashed #2F3345;
    }
  }
  &__group{
    display: grid;
    grid-template-columns: 425px auto;
    position: relative;
    z-index: 2;
  }
  &__left,
  &__right{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__left{
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      left: 0;
      top: 24.5px;
      border-top: 1px dashed #2F3345;
    }
    &--main{
      &:before{
        display: none;
      }
    }
  }
  &__link{
    margin-right: auto;
    padding: 0 30px;
    height: 49px;
    font-weight: 500;
    background-color: #fff;
    position: relative;
    z-index: 2;
    &--main{
      background-color: $black;
      color: #fff;
      &:hover{
        background-color: darken($black,10%);
      }
    }
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }
}


@media screen and (max-width: 992px){
  .site-map{
    padding-bottom: 80px;
    &__grid{
      gap: 30px;
      margin-top: 32px;
    }
    &__link{
      text-align: center;
      padding: 0 20px;
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
    &__group{
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__left{
      &:before{
        display: none;
      }
    }
    &__right{
      padding-left: 25px;
    }
  }
}
